import { AssetBundleID } from "../../../../../scripts/Defines/Defines";
import AssetCache from "../../../../../scripts/Frameworks/AssetCache/AssetCache";
import EntityNodePool from "../../../../../scripts/Frameworks/EWS/Entity/EntityNodePool";
import WorldBase from "../../../../../scripts/Frameworks/EWS/World/WorldBase";
import TileBase, { TileType } from "./TileBase";

const { ccclass, property } = cc._decorator;
@ccclass
export default class Tile extends TileBase {

    public static create(world: WorldBase, level: number): Tile {
        let entity = EntityNodePool.getInstance().getEntityNode(AssetBundleID.GameScene, "prefab/game_layer/Tile", "Tile");
        if (entity) {
            let tile: Tile = entity as Tile;
            tile.initWithLevel(world, level);
            return tile;
        }
        return null;
    }

    @property(cc.Label)
    label: cc.Label = null;

    @property(cc.Sprite)
    tile: cc.Sprite = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

    }

    // update (dt) {}

    private static MaxLevel = 7;
    private static MinLevel = 1;
    private _level: number = Tile.MinLevel;
    public getLevel(): number {
        return this._level;
    }

    public setLevel(level: number): void {
        this._level = Math.max(Tile.MinLevel, Math.min(level, Tile.MaxLevel));
        this.label.string = this._level.toString();

        let frameName = "texture/game_layer/kuaizi_" + this._level;
        let spriteFrame = AssetCache.getInstance().getAssetSync(AssetBundleID.GameScene, frameName, cc.SpriteFrame);
        if (spriteFrame) {
            this.tile.spriteFrame = spriteFrame as cc.SpriteFrame
        }
    }

    protected initWithLevel(world: WorldBase, level: number) {
        this.initWithType(world, TileType.Tile);
        this.setLevel(level);
    }

    public onRecycle() {
        super.onRecycle();
        this.node.rotation = 0;
        this.node.scale = 1;
        this.node.anchorX = 0.5;
        this.node.anchorY = 0.5;
        this.node.opacity = 255;
        this.node.color = cc.color(255, 255, 255, 255);
    }
}
