import GameSceneStateBase, { GameSceneStateType } from "./GameSceneStateBase";
import GameScene from "../GameScene";
import GameSceneStatePool from "./GameSceneStatePool";
import GameLayer from "../GameLayer/GameLayer";
import GameSceneStatePlaying from "./GameSceneStatePlaying";

export default class GameSceneStateCreate extends GameSceneStateBase {
    public static create(): GameSceneStateCreate {
        let instance = GameSceneStatePool.getInstance().getState(GameSceneStateType.Create) as GameSceneStateCreate
        if (!instance) {
            instance = new GameSceneStateCreate();
        }
        instance.init()
        return instance;
    }

    constructor() {
        super(GameSceneStateType.Create);
    }

    private init() {

    }

    public enter(onwer: GameScene): void {
        this.createGameLayer(onwer);
        onwer.setCurrentState(GameSceneStatePlaying.create())
    }

    private createGameLayer(onwer: GameScene) {
        let layer = GameLayer.create();
        onwer.setGameLayer(layer);
        layer.init();
    }

}
