import SimpleStateMachine from "../../../../scripts/Frameworks/EWS/FSM/SimpleStateMachine";
import GameScene from "../GameScene";
import GameSceneStateBase from "./GameSceneStateBase";
import GameSceneStatePool from "./GameSceneStatePool";

export default class GameSceneStateMachine extends SimpleStateMachine<GameScene> {
    public static create(onwer): GameSceneStateMachine {
        let instance = new GameSceneStateMachine(onwer);
        return instance;
    }

    constructor(onwer: GameScene) {
        super(onwer);
    }

    public setCurrentState(newState: GameSceneStateBase): void {
        let oldState = this.getCurrentState();
        super.setCurrentState(newState);
        GameSceneStatePool.getInstance().putState(oldState)
    }

}
