import GameLayerStateBase, { GameLayerStateType } from "./GameLayerStateBase";
import GameLayerStatePool from "./GameLayerStatePool";
import GameLayer from "../GameLayer";
import { TilePieceInfo } from "../TilePiece/TilePiece";
import GameLayerStateMatch from "./GameLayerStateMatch";
import TileMapStateGameOver from "../TileMap/State/TileMapStateGameOver";
import TileMapStateInlay from "../TileMap/State/TileMapStateInlay";
import GameLayerStateGameOver from "./GameLayerStateGameOver";

export default class GameLayerStateInlay extends GameLayerStateBase {
    public static create(): GameLayerStateInlay {
        let instance = GameLayerStatePool.getInstance().getState(GameLayerStateType.Inlay) as GameLayerStateInlay
        if (!instance) {
            instance = new GameLayerStateInlay();
        }
        instance.init()
        return instance;
    }

    constructor() {
        super(GameLayerStateType.Inlay)
    }

    private init() {

    }

    private _touchStartPos: cc.Vec2 = cc.v2(0, 0)
    private _tilePieceMoved: boolean = false;
    public enter(onwer: GameLayer): void {
        onwer.getTileMap().setCurrentState(TileMapStateInlay.create())
        this.resetTilePiece(onwer);

        if (!onwer.getTileMap().haveEnoughSlotForTilePiece(onwer.getTilePiece())) {
            onwer.getTileMap().setCurrentState(TileMapStateGameOver.create());
            onwer.setCurrentState(GameLayerStateGameOver.create())
        }
    }

    public onTouchStart(onwer: GameLayer, event: cc.Event.EventTouch): void {
        let pos = onwer.getTilePiece().node.parent.convertToNodeSpaceAR(event.getLocation());
        this._touchStartPos = cc.v2(pos.x, pos.y);
        this._tilePieceMoved = false;
        onwer.getTilePiece().node.stopActionByTag(1001);
        onwer.getTilePiece().setPosition(cc.v2(0, 0));
    }
    public onTouchMove(onwer: GameLayer, event: cc.Event.EventTouch): void {
        let pos = onwer.getTilePiece().node.parent.convertToNodeSpaceAR(event.getLocation());
        let offset = cc.v2(pos.x, pos.y).sub(this._touchStartPos);
        if (offset.magSqr() > 100) {
            this._tilePieceMoved = true;
        }
        onwer.getTilePiece().setPosition(offset);

    }
    public onTouchEnd(onwer: GameLayer, event: cc.Event.EventTouch): void {
        if (!this._tilePieceMoved) {
            onwer.getTilePiece().rotate()
        } else {
            let updateLogicalPositions = onwer.getTileMap().insertTilePiece(onwer.getTilePiece())
            if (updateLogicalPositions.length > 0) {
                this.hidePiece(onwer);
                onwer.setCurrentState(GameLayerStateMatch.create(updateLogicalPositions));
            } else {
                this.moveTilePieceBack(onwer);
            }
        }
    }
    public onTouchCancel(onwer: GameLayer, event: cc.Event.EventTouch): void {
        this.onTouchEnd(onwer, event);
    }

    private moveTilePieceBack(onwer: GameLayer) {
        let move = cc.moveTo(0.25, cc.v2(0, 0)).easing(cc.easeOut(2))
        move.setTag(1001);
        onwer.getTilePiece().node.stopActionByTag(1001);
        onwer.getTilePiece().node.runAction(move);
    }

    private resetTilePiece(onwer: GameLayer) {

        let random = Math.floor(Math.random() * 21);

        let levels: number[] = []
        if (random < 6) {
            let level = Math.floor(Math.random() * 6 + 1);
            levels.push(level)
        } else {
            let level1 = Math.floor(Math.random() * 4 + 1);
            let level2 = 0;
            while (1) {
                level2 = Math.floor(Math.random() * 4 + 1);
                if (level2 != level1) {
                    break;
                }
            }
            levels.push(level1)
            levels.push(level2)
        }
        let angleIndex = Math.floor(Math.random() * 6);
        let tilePiece = onwer.getTilePiece();
        tilePiece.node.active = true;
        tilePiece.setTilePieceInfo(new TilePieceInfo(levels, angleIndex))
        tilePiece.setPosition(cc.v2(0, 0));
    }

    private hidePiece(onwer: GameLayer) {
        onwer.getTilePiece().node.active = false;
    }

}
