import { AssetBundleID, SceneName } from "../../../../scripts/Defines/Defines";
import IGame from "../../../../scripts/Frameworks/IGame/IGame";
import LayerBase from "../../../../scripts/Frameworks/LayerDirector/LayerBase";
import LayerDirectorError from "../../../../scripts/Frameworks/LayerDirector/LayerDirectorError";
import LayerEnterParamBase from "../../../../scripts/Frameworks/LayerDirector/LayerEnterParamBase";
import PreloadBundleInfo from "../../../../scripts/Frameworks/SceneDirector/PreloadAssetInfo";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PauseLayer extends LayerBase {

    public static readonly LayerName = "PauseLayer";
    public static show(param: LayerEnterParamBase,
        completeCallback: (error: LayerDirectorError, layer: PauseLayer) => void) {
        IGame.getInstance().addLayerWithPrefab(AssetBundleID.GameScene, "prefab/ui_layer/PauseLayer",
            PauseLayer.LayerName,
            param,
            (error: LayerDirectorError, layer: LayerBase) => {
                completeCallback && completeCallback(error, layer as PauseLayer)
            })
    }

    public getLayerName(): string {
        return PauseLayer.LayerName;
    }

    @property(cc.Node)
    giveUpBtn: cc.Node = null

    @property(cc.Node)
    resumeBtn: cc.Node = null


    public onLayerLoad(): void {
        this.giveUpBtn.on(cc.Node.EventType.TOUCH_END, this.onGiveUpBtn, this);
        this.resumeBtn.on(cc.Node.EventType.TOUCH_END, this.onResumeBtn, this);

    }
    public onLayerEnable(): void {

    }
    public onLayerEnter(param: LayerEnterParamBase): void {

    }
    public onLayerStart(): void {

    }
    public onLayerUpdate(dt: number): void {

    }
    public onLayerExit(): void {

    }
    public onLayerDisable(): void {

    }
    public onLayerDestroy(): void {

    }

    private onGiveUpBtn(event: cc.Event.EventTouch) {
        let preloadInfo = PreloadBundleInfo.create();
        preloadInfo.extiBundles = [AssetBundleID.GameScene];
        preloadInfo.enterBundles[AssetBundleID.MainScene] = [];
        IGame.getInstance().replaceSceneWithSwitchScene(preloadInfo, SceneName.MainScene);
    }

    private onResumeBtn(event: cc.Event.EventTouch) {
        this.close();
    }

}
