import GameLayerStateBase, { GameLayerStateType } from "./GameLayerStateBase";
import GameLayerStatePool from "./GameLayerStatePool";
import GameLayer from "../GameLayer";
import TileMapStateCheckMatch from "../TileMap/State/TileMapStateCheckMatch";

export default class GameLayerStateMatch extends GameLayerStateBase {

    public static create(positions: cc.Vec2[]): GameLayerStateMatch {
        let instance = GameLayerStatePool.getInstance().getState(GameLayerStateType.Match) as GameLayerStateMatch
        if (!instance) {
            instance = new GameLayerStateMatch();
        }
        instance.init(positions)
        return instance;
    }

    private _updateLogicalPositions: cc.Vec2[] = []

    constructor() {
        super(GameLayerStateType.Match)
    }

    private init(positions: cc.Vec2[]) {
        this._updateLogicalPositions = positions;
    }

    public enter(onwer: GameLayer): void {
        onwer.getTileMap().setCurrentState(TileMapStateCheckMatch.create(this._updateLogicalPositions));
    }
}
