export class MergePathNode {
    public readonly fromIndex: number = 0;
    public readonly position: cc.Vec2 = cc.v2(0, 0)

    constructor(fromIndex: number, position: cc.Vec2) {
        this.fromIndex = fromIndex;
        this.position = position;
    }

    public static create(fromIndex: number, position: cc.Vec2): MergePathNode {
        let instance = new MergePathNode(fromIndex, position);
        return instance
    }
}

export default class MegerPath {
    public readonly nodes: ReadonlyArray<MergePathNode> = [];
    public readonly megerTileLevel: number = 0
    constructor(nodes: MergePathNode[], level: number) {
        this.nodes = nodes;
        this.megerTileLevel = level;
    }

    public static create(nodes: MergePathNode[], level: number): MegerPath {
        let instance = new MegerPath(nodes, level);
        return instance
    }

    public findIndex(pos: cc.Vec2): number {
        let index = this.nodes.findIndex((node, index) => {
            return node.position.x == pos.x && node.position.y == pos.y
        })
        return index
    }

    public isContainPos(pos: cc.Vec2): boolean {
        return this.findIndex(pos) != -1
    }

    public getMegerPosition(): cc.Vec2 {
        return this.nodes[0].position;
    }
}
