import SimpleStateMachine from "../../../../../scripts/Frameworks/EWS/FSM/SimpleStateMachine";
import GameLayer from "../GameLayer";
import GameLayerStateBase from "./GameLayerStateBase";
import GameLayerStatePool from "./GameLayerStatePool";

export default class GameLayerStateMachine extends SimpleStateMachine<GameLayer> {
    public static create(onwer): GameLayerStateMachine {
        let instance = new GameLayerStateMachine(onwer);
        return instance;
    }

    constructor(onwer: GameLayer) {
        super(onwer);
    }

    public setCurrentState(newState: GameLayerStateBase): void {
        let oldState = this.getCurrentState();
        super.setCurrentState(newState);
        GameLayerStatePool.getInstance().putState(oldState)
    }

}
