import TileMapStateMachine from "./State/TileMapStateMachine";
import TileMapStateCreate from "./State/TileMapStateCreate";
import TileMapStateBase, { TileMapStateType } from "./State/TileMapStateBase";
import TileSlot from "../Tile/TileSlot";
import Tile from "../Tile/Tile";
import TilePiece from "../TilePiece/TilePiece";
import TileMapStateInlay from "./State/TileMapStateInlay";
import GameLayer from "../GameLayer";
import EntityBase from "../../../../../scripts/Frameworks/EWS/Entity/EntityBase";
import WorldBase from "../../../../../scripts/Frameworks/EWS/World/WorldBase";
import AssetCache from "../../../../../scripts/Frameworks/AssetCache/AssetCache";
import { AssetBundleID } from "../../../../../scripts/Defines/Defines";
import GameMath from "../../../../../scripts/Frameworks/Utils/GameMath";




const { ccclass, property } = cc._decorator;

export enum TileMapDirection {
    None = 0,
    Right,
    RightTop,
    LeftTop,
    Left,
    LeftBottom,
    RightBottom,
}

@ccclass
export default class TileMap extends EntityBase {

    public static create(world: WorldBase): TileMap {
        let node: cc.Node = null;
        let res = AssetCache.getInstance().getAssetSync(AssetBundleID.GameScene, "prefab/game_layer/TileMap", cc.Prefab);
        if (res) {
            node = cc.instantiate(res as cc.Prefab);
        }
        if (node) {
            node.setPosition(cc.v2(0, 0));
            let component = node.getComponent(TileMap);
            return component;
        }
        return null;
    }

    public init(world: WorldBase) {
        if (this._stateMachine == null) {
            super.initEntity(world);
            this._stateMachine = TileMapStateMachine.create(this)
            let width = 90;
            let height = Math.round(width * Math.sin(GameMath.degreeToRadian(60)))// height = sin60 * width
            this.setCurrentState(TileMapStateCreate.create(cc.size(7, 7), cc.size(width, height)))
        }
    }

    @property(cc.Node)
    slotRoot: cc.Node = null
    public getTileRoot(): cc.Node {
        return this.slotRoot;
    }

    private _gameLayer: GameLayer = null;
    public getGameLayer(): GameLayer {
        return this._gameLayer;
    }

    public setGameLayer(gameLayer: GameLayer): void {
        this._gameLayer = gameLayer;
    }

    private _tileSize: cc.Size = cc.size(90, 78);// height = sin60 * width
    public getTileSize(): cc.Size {
        return this._tileSize
    }
    public setTileSize(size: cc.Size): void {
        this._tileSize = size;
    }
    private _mapSize: cc.Size = cc.size(7, 7)
    public getMapSize(): cc.Size {
        return this._mapSize
    }
    public setMapSize(size: cc.Size): void {
        this._mapSize = size;
    }

    private _origin: cc.Vec2 = cc.v2(0, 0);
    public getOrigin(): cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin: cc.Vec2): void {
        this._origin = origin;
    }

    private _evenRowDirectionToOffsetDict = {};
    public getEvenRowDirectionToOffsetDict(): Object {
        return this._evenRowDirectionToOffsetDict;
    }
    public setEvenRowDirectionToOffsetDict(dict: Object): void {
        this._evenRowDirectionToOffsetDict = dict;
    }
    private _oddRowDirectionToOffsetDict = {};
    public getOddRowDirectionToOffsetDict(): Object {
        return this._oddRowDirectionToOffsetDict;
    }
    public setOddRowDirectionToOffsetDict(dict: Object): void {
        this._oddRowDirectionToOffsetDict = dict;
    }

    private _stateMachine: TileMapStateMachine = null
    public getCurrentState(): TileMapStateBase {
        return this._stateMachine.getCurrentState() as TileMapStateBase;
    }
    public setCurrentState(state: TileMapStateBase) {
        this._stateMachine.setCurrentState(state);
    }

    private isSameWithCenter(row: number): boolean {
        let isSameWithCenter = Math.floor(this._mapSize.height * 0.5) % 2 == row % 2
        return isSameWithCenter;
    }

    public getPixelPosWithLogicalPos(pos: cc.Vec2): cc.Vec2 {
        let isSameWithCenter = this.isSameWithCenter(pos.y);
        let leftStart = this._tileSize.width * 0.5;
        if (isSameWithCenter) {
            leftStart = 0;
        }

        let x = leftStart + pos.x * this._tileSize.width;
        let y = pos.y * this._tileSize.height;
        return cc.v2(x, y).add(this._origin);
    }

    public getLogicalPosWithPixelPos(pos: cc.Vec2): cc.Vec2 {
        let origin = this._origin.sub(cc.v2(this._tileSize.width * 0.5, this._tileSize.height * 0.5));
        let offset = pos.sub(origin);
        let row = Math.floor(offset.y / this._tileSize.height)

        let isSameWithCenter = this.isSameWithCenter(row);
        let leftStart = this._tileSize.width * 0.5;
        if (isSameWithCenter) {
            leftStart = 0;
        }
        let column = Math.floor((offset.x - leftStart) / this._tileSize.width)
        return cc.v2(column, row);
    }

    public getOffsetWithPositionAndDirection(pos: cc.Vec2, direction: TileMapDirection): cc.Vec2 {
        let isSameWithCenter = this.isSameWithCenter(pos.y);
        if (isSameWithCenter) {
            return this._oddRowDirectionToOffsetDict[direction];
        } else {
            return this._evenRowDirectionToOffsetDict[direction];
        }
    }

    public getAllNeighbourPositions(pos: cc.Vec2): cc.Vec2[] {
        let result: cc.Vec2[] = [];
        let directions: TileMapDirection[] = [
            TileMapDirection.Right,
            TileMapDirection.RightTop,
            TileMapDirection.LeftTop,
            TileMapDirection.Left,
            TileMapDirection.LeftBottom,
            TileMapDirection.RightBottom,
        ];

        for (let index = 0; index < directions.length; index++) {
            let direction = directions[index];
            let offset = this.getOffsetWithPositionAndDirection(pos, direction);
            let neighbourPos = pos.add(offset);
            result.push(neighbourPos);
        }
        return result;
    }

    public getKeyWithLogicalPos(pos: cc.Vec2): string {
        return pos.toString();
    }

    private _tileSlots: { [key: string]: TileSlot } = {};
    public addSlotWithLogicalPos(slot: TileSlot, pos: cc.Vec2): void {
        this.removeSlotWithLogicalPos(pos);
        slot.setLogicalPostion(pos);
        slot.node.setPosition(this.getPixelPosWithLogicalPos(pos));
        this.slotRoot.addChild(slot.node, 0);
        let key = this.getKeyWithLogicalPos(pos);
        this._tileSlots[key] = slot;

    }

    public removeSlotWithLogicalPos(pos: cc.Vec2): TileSlot {
        let key = this.getKeyWithLogicalPos(pos);
        let slot: TileSlot = this._tileSlots[key];
        if (slot) {
            slot.node.removeFromParent(false);
        }
        delete this._tileSlots[key];
        return slot;
    }

    public getSlotWithLogicalPos(pos: cc.Vec2): TileSlot {
        let key = this.getKeyWithLogicalPos(pos)
        return this._tileSlots[key];
    }

    public getAllSlots(): { [key: string]: TileSlot } {
        return this._tileSlots;
    }

    public getAllValidPositions(): cc.Vec2[] {
        let result: cc.Vec2[] = [];
        for (const key in this._tileSlots) {
            const slot = this._tileSlots[key];
            let logicalPos = slot.getLogicalPosition();
            result.push(logicalPos);
        }
        return result;
    }

    private _tiles: { [key: string]: Tile } = {};
    public addTileWithLogicalPos(tile: Tile, pos: cc.Vec2): void {
        let slot = this.getSlotWithLogicalPos(pos)
        if (slot) {
            this.removeTileWithLogicalPos(pos);
            tile.setLogicalPostion(pos);
            tile.node.setPosition(this.getPixelPosWithLogicalPos(pos));
            this.slotRoot.addChild(tile.node, 1);
            let key = this.getKeyWithLogicalPos(pos);
            this._tiles[key] = tile;
        }
    }

    public removeTileWithLogicalPos(pos: cc.Vec2): Tile {
        let key = this.getKeyWithLogicalPos(pos);
        let tile: Tile = this._tiles[key];
        if (tile) {
            tile.node.removeFromParent(false);
        }
        delete this._tiles[key];
        return tile;
    }

    public getTileWithLogicalPos(pos: cc.Vec2): Tile {
        let key = this.getKeyWithLogicalPos(pos)
        return this._tiles[key];
    }


    public isTileSlotEmptyAtLogicalPos(pos: cc.Vec2): boolean {
        let slot = this.getSlotWithLogicalPos(pos);
        if (slot) {
            let tile = this.getTileWithLogicalPos(pos);
            if (!tile) {
                return true
            }
        }
        return false;
    }

    public insertTilePiece(tilePiece: TilePiece): cc.Vec2[] {
        let state = this.getCurrentState()
        if (state.getType() == TileMapStateType.Inlay) {
            let idleState = state as TileMapStateInlay
            return idleState.inlayTilePiece(this, tilePiece);
        }
        return []
    }


    public haveEnoughSlotForTilePiece(tilePiece: TilePiece): boolean {
        let state = this.getCurrentState()
        if (state.getType() == TileMapStateType.Inlay) {
            let inlayState = state as TileMapStateInlay
            return inlayState.haveEnoughSlotForTilePiece(this, tilePiece);
        }
        return false
    }


    public addAnimationTileWithLogicalPos(tile: Tile, pos: cc.Vec2): void {
        tile.node.setPosition(this.getPixelPosWithLogicalPos(pos));
        this.slotRoot.addChild(tile.node, 2);
    }

    public removeAnimationTile(tile: Tile) {
        tile.recycle()
    }

}
