import TileMapStateBase, { TileMapStateType } from "./TileMapStateBase";
import TileMapStatePool from "./TileMapStatePool";
import TileMap from "../TileMap";
import GameLayerStateInlay from "../../State/GameLayerStateInlay";
import MegerPath, { MergePathNode } from "../../GameDefines/MegerPath";
import GameLayerStateMeger from "../../State/GameLayerStateMeger";

export default class TileMapStateCheckMatch extends TileMapStateBase {

    public static create(positions: cc.Vec2[]): TileMapStateCheckMatch {
        let instance: TileMapStateCheckMatch = TileMapStatePool.getInstance().getState(TileMapStateType.CheckMatch) as TileMapStateCheckMatch
        if (!instance) {
            instance = new TileMapStateCheckMatch();
        }
        instance.init(positions);
        return instance;
    }

    private _updateLogicalPositions: cc.Vec2[] = [];

    constructor() {
        super(TileMapStateType.CheckMatch)
    }

    public init(positions: cc.Vec2[]) {
        this._updateLogicalPositions = positions;
    }

    public enter(onwer: TileMap): void {

        this._updateLogicalPositions.sort((a: cc.Vec2, b: cc.Vec2) => {
            let tileA = onwer.getTileWithLogicalPos(a);
            let tileB = onwer.getTileWithLogicalPos(b);
            return tileA.getLevel() - tileB.getLevel()
        })


        let megerPaths: MegerPath[] = []
        for (let index = 0; index < this._updateLogicalPositions.length; index++) {
            let checkPos = this._updateLogicalPositions[index];
            let willMeger = false
            for (let j = 0; j < megerPaths.length; j++) {
                let path = megerPaths[j];
                if (path.isContainPos(checkPos)) {
                    willMeger = true;
                    break;
                }
            }
            if (!willMeger) {
                let path = this.getMegerPathFromPos(onwer, checkPos);
                if (path.megerTileLevel > 0) {
                    megerPaths.push(path);
                }
            }
        }

        if (megerPaths.length > 0) {
            onwer.getGameLayer().setCurrentState(GameLayerStateMeger.create(megerPaths))
        } else {
            onwer.getGameLayer().setCurrentState(GameLayerStateInlay.create())
        }
    }

    private getMegerPathFromPos(onwer: TileMap, pos: cc.Vec2): MegerPath {

        let pathNodes: MergePathNode[] = [];
        let checkedPositions: cc.Vec2[] = [];
        pathNodes.push(MergePathNode.create(-1, pos));
        checkedPositions.push(pos);
        for (let i = 0; i < pathNodes.length; i++) {
            let pathNode = pathNodes[i];
            let tile = onwer.getTileWithLogicalPos(pathNode.position);
            let neighbourPositions = onwer.getAllNeighbourPositions(pathNode.position);
            for (let j = 0; j < neighbourPositions.length; j++) {
                let neighbourPos = neighbourPositions[j];
                if (!this.isPostionChecked(checkedPositions, neighbourPos)) {
                    checkedPositions.push(neighbourPos);
                    let neighbourTile = onwer.getTileWithLogicalPos(neighbourPos)
                    if (neighbourTile && tile.getLevel() == neighbourTile.getLevel()) {
                        pathNodes.push(MergePathNode.create(i, neighbourPos));
                    }
                }
            }
        }

        let level = 0
        if (pathNodes.length >= 3) {
            let tile = onwer.getTileWithLogicalPos(pos);
            level = tile.getLevel() + 1
        }
        return MegerPath.create(pathNodes, level);
    }

    private isPostionChecked(checkedPositions: cc.Vec2[], pos: cc.Vec2) {
        let index = checkedPositions.findIndex((value: cc.Vec2, index: number) => {
            return value.x == pos.x && value.y == pos.y
        })
        return index != -1
    }

}
