import TileMapStateBase, { TileMapStateType } from "./TileMapStateBase";
import TileMapStatePool from "./TileMapStatePool";
import TileMap from "../TileMap";
import MegerPath from "../../GameDefines/MegerPath";
import Tile from "../../Tile/Tile";
import GameLayerStateMatch from "../../State/GameLayerStateMatch";


class NewTileFinishMegerData {
    public tileMap: TileMap;
    public megerPath: MegerPath;

    constructor(tileMap: TileMap, megerPath: MegerPath) {
        this.tileMap = tileMap;
        this.megerPath = megerPath;
    }
}

class OldTileFinishMegerData {
    public tileMap: TileMap;
    public tile: Tile;

    constructor(tileMap: TileMap, tile: Tile) {
        this.tileMap = tileMap;
        this.tile = tile;
    }
}


export default class TileMapStateMeger extends TileMapStateBase {

    public static create(megerPaths: MegerPath[]): TileMapStateMeger {
        let instance: TileMapStateMeger = TileMapStatePool.getInstance().getState(TileMapStateType.Meger) as TileMapStateMeger
        if (!instance) {
            instance = new TileMapStateMeger();
        }
        instance.init(megerPaths);
        return instance;
    }

    private _megerPaths: MegerPath[] = [];
    private _pendMegerPath: MegerPath[] = []
    private _pendingTiles: Tile[][] = [];

    private readonly _moveDuration: number = 0.25;
    private readonly _disappearDuration: number = 0.25;
    constructor() {
        super(TileMapStateType.Meger)
    }

    public init(megerPaths: MegerPath[]) {
        this._megerPaths = megerPaths;

    }

    public enter(onwer: TileMap): void {
        for (let index = 0; index < this._megerPaths.length; index++) {
            let path = this._megerPaths[index];
            this.createNewTileMegerAnimation(onwer, path);
        }
    }

    public exit(onwer: TileMap): void {
        this._megerPaths.length = 0;
    }


    private createNewTileMegerAnimation(onwer: TileMap, path: MegerPath) {
        this._pendMegerPath.push(path);
        for (let index = path.nodes.length - 1; index > -1; index--) {
            let nodeIndexes = this.getNodeIndexesWithPathIndex(path, index);
            this.createOldTileMegerAnimation(onwer, path, nodeIndexes)
        }

        let newTile = Tile.create(onwer.getWorld(), path.megerTileLevel)
        onwer.addTileWithLogicalPos(newTile, path.getMegerPosition())
        newTile.node.scale = 0;

        let delay = cc.delayTime(this._moveDuration)
        let appear = cc.spawn(
            cc.scaleTo(0.3, 1).easing(cc.easeBackOut()),
            cc.fadeIn(0.3),
        )
        let action = cc.sequence(delay, appear, cc.callFunc(this.newTileFinishMeger, this, new NewTileFinishMegerData(onwer, path)))
        newTile.node.runAction(action);
    }

    private getNodeIndexesWithPathIndex(path: MegerPath, index: number): number[] {
        let result: number[] = [];
        while (index > -1) {
            let node = path.nodes[index];
            result.push(index);
            index = node.fromIndex;
        }
        return result;
    }

    private newTileFinishMeger(node: cc.Node, data: NewTileFinishMegerData) {
        let tileMap: TileMap = data.tileMap;
        let megerPath: MegerPath = data.megerPath;
        let index = this._pendMegerPath.indexOf(megerPath);
        if (index != -1) {
            this._pendMegerPath.splice(index, 1);
        }

        let checkPostions: cc.Vec2[] = []
        for (let index = 0; index < this._megerPaths.length; index++) {
            let path = this._megerPaths[index];
            checkPostions.push(path.getMegerPosition())
        }
        if (this._pendingTiles.length <= 0) {
            tileMap.getGameLayer().setCurrentState(GameLayerStateMatch.create(checkPostions))
        }
    }

    private createOldTileMegerAnimation(onwer: TileMap, path: MegerPath, indexes: number[],) {
        let startNode = path.nodes[indexes[0]];
        let tile = onwer.removeTileWithLogicalPos(startNode.position);
        onwer.addAnimationTileWithLogicalPos(tile, startNode.position);
        this._pendMegerPath.push(path)
        let moveAction: cc.FiniteTimeAction[] = []
        if (indexes.length > 1) {

            let stepDuration = this._moveDuration / (indexes.length - 1);
            for (let i = 1; i < indexes.length; i++) {
                let nodeIndex = indexes[i];
                let node = path.nodes[nodeIndex];
                let pixelPos = onwer.getPixelPosWithLogicalPos(node.position)
                let move = cc.moveTo(stepDuration, pixelPos);
                moveAction.push(move);
            }
        } else {
            moveAction.push(cc.delayTime(this._moveDuration))
        }

        moveAction.push(cc.spawn(cc.fadeOut(this._disappearDuration).easing(cc.easeOut(2)), cc.scaleTo(this._disappearDuration, 0)))
        moveAction.push(cc.callFunc(this.oldTileFinishMerge, this, new OldTileFinishMegerData(onwer, tile)))
        let action = cc.sequence(moveAction)
        tile.node.runAction(action);
    }


    private oldTileFinishMerge(node: cc.Node, data: OldTileFinishMegerData) {
        let tileMap: TileMap = data.tileMap;
        let tile = data.tile;
        tileMap.removeAnimationTile(tile);
    }
}
