import PauseLayer from "./UILayer/PauseLayer";
import GameSceneStateMachine from "./State/GameSceneStateMachine";
import GameSceneStateBase from "./State/GameSceneStateBase";
import GameSceneStateCreate from "./State/GameSceneStateCreate";
import GameLayer, { GameLayerDelegate } from "./GameLayer/GameLayer";
import GameSceneStateOver from "./State/GameSceneStateOver";
import GameOverLayer, { GameOverLayerDelegate } from "./UILayer/GameOverLayer";
import SceneBase from "../../../scripts/Frameworks/SceneDirector/SceneBase";
import IGame from "../../../scripts/Frameworks/IGame/IGame";
import LayerDirectorError from "../../../scripts/Frameworks/LayerDirector/LayerDirectorError";
import { AssetBundleID, SceneName } from "../../../scripts/Defines/Defines";
import PreloadBundleInfo from "../../../scripts/Frameworks/SceneDirector/PreloadAssetInfo";
import SceneEnterParamBase from "../../../scripts/Frameworks/SceneDirector/SceneEnterParamBase";


export class GameSceneEnterParam extends SceneEnterParamBase {

}

const { ccclass, property } = cc._decorator;
@ccclass
export default class GameScene extends SceneBase implements GameLayerDelegate, GameOverLayerDelegate {

    public getSceneName(): string {
        return SceneName.GameScene;
    }

    @property(cc.Node)
    gameLayerRoot: cc.Node = null;

    @property(cc.Node)
    pauseBtn: cc.Node = null;

    private _gameLayer: GameLayer = null
    public getGameLayer(): GameLayer {
        return this._gameLayer;
    }
    public setGameLayer(layer: GameLayer): void {
        if (this._gameLayer) {
            this._gameLayer.node.destroy();
            this._gameLayer = null;
        }
        if (layer) {
            this._gameLayer = layer;
            this._gameLayer.setDelegate(this);
            this.gameLayerRoot.addChild(this._gameLayer.node);
        }
    }

    private _stateMachine: GameSceneStateMachine = null;
    public getCurrentState(): GameSceneStateBase {
        return this._stateMachine.getCurrentState() as GameSceneStateBase
    }
    public setCurrentState(state: GameSceneStateBase) {
        this._stateMachine.setCurrentState(state);
    }

    public onSceneLoad(): void { }
    public onSceneEnable(): void { }
    public onSceneEnter(param: SceneEnterParamBase) {
        this._stateMachine = GameSceneStateMachine.create(this);
        this.setCurrentState(GameSceneStateCreate.create())
        this.pauseBtn.on(cc.Node.EventType.TOUCH_END, this.onPauseBtn, this);
    }
    public onSceneStart(): void { }
    public onSceneUpdate(dt: number): void { }
    public onSceneDisable(): void { }
    public onSceneExit(): void { }
    public onSceneDestroy(): void { }

    public onPauseBtn(event: cc.Event.EventTouch) {
        PauseLayer.show(null, (error: LayerDirectorError, layer: PauseLayer) => {

        })
    }

    replay(layer: GameOverLayer) {
        this.setCurrentState(GameSceneStateCreate.create());
    }

    onReadyForNextPiece(gameLayer: GameLayer) {

    }

    onGameOver(gameLayer: GameLayer) {
        this.setCurrentState(GameSceneStateOver.create());
    }

    backToMain(layer: GameOverLayer) {
        let preloadInfo = PreloadBundleInfo.create();
        preloadInfo.extiBundles = [AssetBundleID.GameScene];
        preloadInfo.enterBundles[AssetBundleID.MainScene] = [];
        IGame.getInstance().replaceSceneWithSwitchScene(preloadInfo, SceneName.MainScene);
    }
}
