import EntityBase from "../../../../../scripts/Frameworks/EWS/Entity/EntityBase";
import WorldBase from "../../../../../scripts/Frameworks/EWS/World/WorldBase";

export enum TileType {
    None,
    Tile,
    Slot,
}

const { ccclass, property } = cc._decorator;

@ccclass
export default class TileBase extends EntityBase {

    private _type: TileType = TileType.None;
    public getType(): TileType {
        return this._type;
    }
    protected initWithType(world: WorldBase, type: TileType) {
        super.initEntity(world);
        this._type = type;
    }

    private _position: cc.Vec2 = cc.v2(0, 0);
    public getLogicalPosition(): cc.Vec2 {
        return this._position;
    }
    public setLogicalPostion(pos: cc.Vec2): void {
        this._position = pos;
    }

}
