import { AssetBundleID } from "../../../../scripts/Defines/Defines";
import IGame from "../../../../scripts/Frameworks/IGame/IGame";
import LayerBase from "../../../../scripts/Frameworks/LayerDirector/LayerBase";
import LayerDirectorError from "../../../../scripts/Frameworks/LayerDirector/LayerDirectorError";
import LayerEnterParamBase from "../../../../scripts/Frameworks/LayerDirector/LayerEnterParamBase";

export interface GameOverLayerDelegate {
    backToMain(layer: GameOverLayer);
    replay(layer: GameOverLayer)
}

const { ccclass, property } = cc._decorator;
@ccclass
export default class GameOverLayer extends LayerBase {

    public static readonly LayerName = "GameOverLayer";
    public static show(param?: LayerEnterParamBase,
        completeCallback?: (error: LayerDirectorError, layer: GameOverLayer) => void) {
        IGame.getInstance().addLayerWithPrefab(AssetBundleID.GameScene, "prefab/ui_layer/GameOverLayer",
            GameOverLayer.LayerName,
            param,
            (error: LayerDirectorError, layer: LayerBase) => {
                completeCallback && completeCallback(error, layer as GameOverLayer)
            })
    }

    public getLayerName(): string {
        return GameOverLayer.LayerName;
    }

    @property(cc.Node)
    backToMainBnt: cc.Node = null

    @property(cc.Node)
    replayBtn: cc.Node = null


    private _delegate: GameOverLayerDelegate = null;
    public getDelegate(): GameOverLayerDelegate {
        return this._delegate;
    }
    public setDelegate(delegate: GameOverLayerDelegate): void {
        this._delegate = delegate;
    }

    public onLayerLoad(): void {
        this.backToMainBnt.on(cc.Node.EventType.TOUCH_END, this.onBackToMainBtn, this);
        this.replayBtn.on(cc.Node.EventType.TOUCH_END, this.onReplayBtn, this);

    }
    public onLayerEnable(): void {

    }
    public onLayerEnter(param: LayerEnterParamBase): void {

    }
    public onLayerStart(): void {

    }
    public onLayerUpdate(dt: number): void {

    }
    public onLayerExit(): void {

    }
    public onLayerDisable(): void {

    }
    public onLayerDestroy(): void {

    }

    private onBackToMainBtn(event: cc.Event.EventTouch) {
        this.close();
        if (this._delegate) {
            this._delegate.backToMain(this);
        }
    }

    private onReplayBtn(event: cc.Event.EventTouch) {
        this.close();
        if (this._delegate) {
            this._delegate.replay(this);
        }
    }

}
