import GameSceneStateBase, { GameSceneStateType } from "./GameSceneStateBase";
import GameScene from "../GameScene";
import GameSceneStatePool from "./GameSceneStatePool";
import GameOverLayer from "../UILayer/GameOverLayer";
import LayerDirectorError from "../../../../scripts/Frameworks/LayerDirector/LayerDirectorError";

export default class GameSceneStateOver extends GameSceneStateBase {
    public static create(): GameSceneStateOver {
        let instance = GameSceneStatePool.getInstance().getState(GameSceneStateType.GameOver) as GameSceneStateOver
        if (!instance) {
            instance = new GameSceneStateOver();
        }
        instance.init()
        return instance;
    }

    constructor() {
        super(GameSceneStateType.GameOver);
    }

    private init() {

    }

    public enter(onwer: GameScene): void {
        GameOverLayer.show(null, (error: LayerDirectorError, layer: GameOverLayer) => {
            layer.setDelegate(onwer);
        })
    }

}
