import TileMapStateBase, { TileMapStateType } from "./TileMapStateBase";
import TileMap from "../TileMap";
import TilePiece from "../../TilePiece/TilePiece";
import Tile from "../../Tile/Tile";
import TileMapStatePool from "./TileMapStatePool";

export default class TileMapStateInlay extends TileMapStateBase {

    public static create(): TileMapStateInlay {
        let instance: TileMapStateInlay = TileMapStatePool.getInstance().getState(TileMapStateType.Inlay) as TileMapStateInlay
        if (!instance) {
            instance = new TileMapStateInlay();
        }
        instance.init();
        return instance;
    }

    constructor() {
        super(TileMapStateType.Inlay)
    }

    public init() {

    }

    public inlayTilePiece(tileMap: TileMap, tilePiece: TilePiece): cc.Vec2[] {
        let logicalPositions: cc.Vec2[] = [];
        let tiles = tilePiece.getTiles();
        if (tiles.length > 0) {
            for (let index = 0; index < tiles.length; index++) {
                let tile = tiles[index];
                let worldPos = tile.node.parent.convertToWorldSpaceAR(tile.getPosition());
                let mapPos = tileMap.getTileRoot().convertToNodeSpaceAR(worldPos);
                let logicalPos = tileMap.getLogicalPosWithPixelPos(cc.v2(mapPos));
                logicalPositions.push(logicalPos)
            }

            for (let index = 0; index < logicalPositions.length; index++) {
                let pos = logicalPositions[index];
                if (!tileMap.isTileSlotEmptyAtLogicalPos(pos)) {
                    logicalPositions = [];
                    break;
                }
            }

            for (let index = 0; index < logicalPositions.length; index++) {
                let pos = logicalPositions[index];
                let tile = tiles[index];
                let newTile = Tile.create(tileMap.getWorld(), tile.getLevel());
                tileMap.addTileWithLogicalPos(newTile, pos);
            }
        }
        return logicalPositions;
    }

    public haveEnoughSlotForTilePiece(tileMap: TileMap, tilePiece: TilePiece): boolean {
        let tilePieceCount: number = tilePiece.getLevels().length;
        let validPostions = tileMap.getAllValidPositions();
        for (let index = 0; index < validPostions.length; index++) {
            let logicalPos = validPostions[index];
            if (tileMap.isTileSlotEmptyAtLogicalPos(logicalPos)) {
                if (tilePieceCount == 1) {
                    return true;
                } else if (tilePieceCount == 2) {
                    let neighbourPostions = tileMap.getAllNeighbourPositions(logicalPos)
                    for (let index = 0; index < neighbourPostions.length; index++) {
                        let checkPos = neighbourPostions[index];
                        if (tileMap.isTileSlotEmptyAtLogicalPos(checkPos)) {
                            return true;
                        }
                    }
                }
            }
        }

        return false;
    }
}
