import GameSceneStateBase, { GameSceneStateType } from "./GameSceneStateBase";
import GameSceneStatePool from "./GameSceneStatePool";

export default class GameSceneStatePlaying extends GameSceneStateBase {
    public static create(): GameSceneStatePlaying {
        let instance = GameSceneStatePool.getInstance().getState(GameSceneStateType.Playing) as GameSceneStatePlaying
        if (!instance) {
            instance = new GameSceneStatePlaying();
        }
        instance.init()
        return instance;
    }

    constructor() {
        super(GameSceneStateType.Playing)
    }
    private init() {

    }
}
