import TileMap from "./TileMap/TileMap";
import TilePiece from "./TilePiece/TilePiece";
import GameLayerStateMachine from "./State/GameLayerStateMachine";
import GameLayerStateBase from "./State/GameLayerStateBase";
import GameLayerStateCreate from "./State/GameLayerStateCreate";
import { AssetBundleID } from "../../../../scripts/Defines/Defines";
import WorldBase from "../../../../scripts/Frameworks/EWS/World/WorldBase";
import AssetCache from "../../../../scripts/Frameworks/AssetCache/AssetCache";

const { ccclass, property } = cc._decorator;

export interface GameLayerDelegate {
    onGameOver(gameLayer: GameLayer);
}

@ccclass
export default class GameLayer extends WorldBase {

    public static create(): GameLayer {
        let node: cc.Node = null;
        let res = AssetCache.getInstance().getAssetSync(AssetBundleID.GameScene, "prefab/game_layer/GameLayer", cc.Prefab);
        if (res) {
            node = cc.instantiate(res as cc.Prefab);
        }
        if (node) {
            node.setPosition(cc.v2(0, 0));
            let component = node.getComponent(GameLayer);
            return component;
        }
        return null;
    }

    @property(cc.Node)
    tileMapRoot: cc.Node = null

    @property(cc.Node)
    tilePieceRoot: cc.Node = null;

    private _delegate: GameLayerDelegate = null;
    public getDelegate(): GameLayerDelegate {
        return this._delegate;
    }
    public setDelegate(delegate: GameLayerDelegate): void {
        this._delegate = delegate;
    }

    private _tileMap: TileMap = null
    public getTileMap(): TileMap {
        return this._tileMap;
    }
    public setTileMap(tileMap: TileMap): void {
        if (this._tileMap) {
            this._tileMap.node.destroy();
            this._tilePiece = null;
        }
        if (tileMap) {
            this._tileMap = tileMap;
            this._tileMap.setGameLayer(this);
            this.tileMapRoot.addChild(this._tileMap.node);
        }

    }

    private _tilePiece: TilePiece = null;
    public getTilePiece(): TilePiece {
        return this._tilePiece;
    }
    public setTilePiece(tilePiece: TilePiece): void {
        if (this._tilePiece) {
            this._tilePiece.node.destroy();
            this._tilePiece = null;
        }
        if (tilePiece) {
            this._tilePiece = tilePiece;
            this.tilePieceRoot.addChild(this._tilePiece.node);
        }
    }

    private _stateMachine: GameLayerStateMachine = null;
    public getCurrentState(): GameLayerStateBase {
        return this._stateMachine.getCurrentState() as GameLayerStateBase
    }
    public setCurrentState(state: GameLayerStateBase) {
        this._stateMachine.setCurrentState(state);
    }

    private onTouchStart(event: cc.Event.EventTouch): void {
        let state = this.getCurrentState();
        state.onTouchStart(this, event)
    }

    private onTouchMove(event: cc.Event.EventTouch): void {
        let state = this.getCurrentState();
        state.onTouchMove(this, event)
    }

    private onTouchEnd(event: cc.Event.EventTouch): void {
        let state = this.getCurrentState();
        state.onTouchEnd(this, event)
    }
    private onTouchCancel(event: cc.Event.EventTouch): void {
        let state = this.getCurrentState();
        state.onTouchCancel(this, event)
    }

    public init() {
        if (!this._stateMachine) {
            this._stateMachine = GameLayerStateMachine.create(this);
            this.setCurrentState(GameLayerStateCreate.create())
            this.tilePieceRoot.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this)
            this.tilePieceRoot.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this)
            this.tilePieceRoot.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this)
            this.tilePieceRoot.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchCancel, this)
        }
    }

}
