import GameLayerStateBase, { GameLayerStateType } from "./GameLayerStateBase";
import GameLayerStatePool from "./GameLayerStatePool";
import GameLayer from "../GameLayer";
import TileMapStateGameOver from "../TileMap/State/TileMapStateGameOver";

export default class GameLayerStateGameOver extends GameLayerStateBase {
    public static create(): GameLayerStateGameOver {
        let instance = GameLayerStatePool.getInstance().getState(GameLayerStateType.GameOver) as GameLayerStateGameOver
        if (!instance) {
            instance = new GameLayerStateGameOver();
        }
        instance.init()
        return instance;
    }

    constructor() {
        super(GameLayerStateType.GameOver)
    }

    private init() {

    }

    public enter(onwer: GameLayer): void {
        onwer.getTileMap().setCurrentState(TileMapStateGameOver.create())
        if (onwer.getDelegate()) {
            onwer.getDelegate().onGameOver(onwer);
        }
    }

}
