import { AssetBundleID } from "../../../../../scripts/Defines/Defines";
import EntityNodePool from "../../../../../scripts/Frameworks/EWS/Entity/EntityNodePool";
import WorldBase from "../../../../../scripts/Frameworks/EWS/World/WorldBase";
import TileBase, { TileType } from "./TileBase";

const { ccclass, property } = cc._decorator;
@ccclass
export default class TileSlot extends TileBase {

    public static create(world: WorldBase): TileSlot {
        let entity = EntityNodePool.getInstance().getEntityNode(AssetBundleID.GameScene,
            "prefab/game_layer/TileSlot", "TileSlot");
        if (entity) {
            let tileSlot: TileSlot = entity as TileSlot;
            tileSlot.init(world);
            return tileSlot;
        }
        return null;
    }

    protected init(world: WorldBase) {
        this.initWithType(world, TileType.Slot)
    }
}
