import SimpleStateMachine from "../../../../../../scripts/Frameworks/EWS/FSM/SimpleStateMachine";
import TileMap from "../TileMap";
import TileMapStateBase from "./TileMapStateBase";
import TileMapStatePool from "./TileMapStatePool";

export default class TileMapStateMachine extends SimpleStateMachine<TileMap> {
    public static create(onwer: TileMap): TileMapStateMachine {
        let instance = new TileMapStateMachine(onwer);
        return instance
    }
    constructor(onwer: TileMap) {
        super(onwer);
    }

    public setCurrentState(newState: TileMapStateBase): void {
        let oldState = this.getCurrentState();
        super.setCurrentState(newState);
        TileMapStatePool.getInstance().putState(oldState)
    }
}
