import GameLayerStateBase, { GameLayerStateType } from "./GameLayerStateBase";
import GameLayerStatePool from "./GameLayerStatePool";
import GameLayer from "../GameLayer";
import TileMapStateMeger from "../TileMap/State/TileMapStateMeger";
import MegerPath, { MergePathNode } from "../GameDefines/MegerPath";

export default class GameLayerStateMeger extends GameLayerStateBase {
    public static create(megerPaths: MegerPath[]): GameLayerStateMeger {
        let instance = GameLayerStatePool.getInstance().getState(GameLayerStateType.Meger) as GameLayerStateMeger
        if (!instance) {
            instance = new GameLayerStateMeger();
        }
        instance.init(megerPaths)
        return instance;
    }

    private _megerPaths: MegerPath[] = []

    constructor() {
        super(GameLayerStateType.Meger)
    }

    private init(megerPaths: MegerPath[]) {
        this._megerPaths = megerPaths;
    }

    public enter(onwer: GameLayer): void {
        onwer.getTileMap().setCurrentState(TileMapStateMeger.create(this._megerPaths))
    }

}
