import GameLayerStateBase, { GameLayerStateType } from "./GameLayerStateBase";
import GameLayerStatePool from "./GameLayerStatePool";
import GameLayer from "../GameLayer";
import TileMap from "../TileMap/TileMap";
import TilePiece, { TilePieceInfo } from "../TilePiece/TilePiece";
import GameLayerStateInlay from "./GameLayerStateInlay";

export default class GameLayerStateCreate extends GameLayerStateBase {
    public static create(): GameLayerStateCreate {
        let instance = GameLayerStatePool.getInstance().getState(GameLayerStateType.Create) as GameLayerStateCreate
        if (!instance) {
            instance = new GameLayerStateCreate();
        }
        instance.init()
        return instance;
    }

    constructor() {
        super(GameLayerStateType.Create);
    }

    private init() {

    }

    public enter(onwer: GameLayer): void {
        this.createTileMap(onwer);
        this.createTilePiece(onwer);
        onwer.setCurrentState(GameLayerStateInlay.create())
    }

    private createTileMap(onwer: GameLayer) {
        let tileMap = TileMap.create(onwer);
        onwer.setTileMap(tileMap);
        tileMap.init(onwer);
    }

    private createTilePiece(onwer: GameLayer) {
        let tilePiece = TilePiece.create(onwer, new TilePieceInfo([1], 0));
        onwer.setTilePiece(tilePiece);
    }

}
