import TileMap, { TileMapDirection } from "../TileMap";
import TileMapStateBase, { TileMapStateType } from "./TileMapStateBase";
import TileSlot from "../../Tile/TileSlot";
import TileMapStateInlay from "./TileMapStateInlay";
import TileMapStatePool from "./TileMapStatePool";

export default class TileMapStateCreate extends TileMapStateBase {

    public static create(mapSize: cc.Size, tileSize: cc.Size): TileMapStateCreate {
        let instance: TileMapStateCreate = TileMapStatePool.getInstance().getState(TileMapStateType.Create) as TileMapStateCreate
        if (!instance) {
            instance = new TileMapStateCreate();
        }
        instance.init(mapSize, tileSize);
        return instance;
    }

    private _mapSize: cc.Size = cc.size(0, 0);
    private _tileSize: cc.Size = cc.size(0, 0);

    private constructor() {
        super(TileMapStateType.Create);
    }

    private init(mapSize: cc.Size, tileSize: cc.Size) {
        this._mapSize = mapSize;
        this._tileSize = tileSize;
    }

    public enter(onwer: TileMap): void {
        this.initCoordinate(onwer);
        this.initSlots(onwer);
        onwer.setCurrentState(TileMapStateInlay.create())
    }

    public initCoordinate(onwer: TileMap) {
        let slotRootSize = onwer.getTileRoot().getContentSize();
        let y = (slotRootSize.height - (this._tileSize.height * this._mapSize.height)) * 0.5;
        let x = (slotRootSize.width - (this._tileSize.width * this._mapSize.width)) * 0.5;
        onwer.setTileSize(this._tileSize);
        onwer.setMapSize(this._mapSize);
        onwer.setOrigin(cc.v2(x + this._tileSize.width * 0.5, y + this._tileSize.height * 0.5));

        let evenRowDirectionToOffsetDict = {}
        evenRowDirectionToOffsetDict[TileMapDirection.Right] = cc.v2(1, 0)
        evenRowDirectionToOffsetDict[TileMapDirection.RightTop] = cc.v2(1, 1)
        evenRowDirectionToOffsetDict[TileMapDirection.LeftTop] = cc.v2(0, 1)
        evenRowDirectionToOffsetDict[TileMapDirection.Left] = cc.v2(-1, 0)
        evenRowDirectionToOffsetDict[TileMapDirection.LeftBottom] = cc.v2(0, -1)
        evenRowDirectionToOffsetDict[TileMapDirection.RightBottom] = cc.v2(1, -1)
        onwer.setEvenRowDirectionToOffsetDict(evenRowDirectionToOffsetDict);

        let oddRowDirectionToOffsetDict = {}
        oddRowDirectionToOffsetDict[TileMapDirection.Right] = cc.v2(1, 0)
        oddRowDirectionToOffsetDict[TileMapDirection.RightTop] = cc.v2(0, 1)
        oddRowDirectionToOffsetDict[TileMapDirection.LeftTop] = cc.v2(-1, 1)
        oddRowDirectionToOffsetDict[TileMapDirection.Left] = cc.v2(-1, 0)
        oddRowDirectionToOffsetDict[TileMapDirection.LeftBottom] = cc.v2(-1, -1)
        oddRowDirectionToOffsetDict[TileMapDirection.RightBottom] = cc.v2(0, -1)
        onwer.setOddRowDirectionToOffsetDict(oddRowDirectionToOffsetDict);
    }

    public initSlots(onwer: TileMap) {
        for (let i = 0; i < this._mapSize.height; i++) {
            let delta = Math.abs(Math.floor(i - this.getCenterRow()))
            let startX = 0;
            let endX = this._mapSize.width;
            if (delta % 2 == 0) {
                startX += Math.floor(delta * 0.5)
                endX -= Math.floor(delta * 0.5);
            } else {
                startX += Math.floor(delta * 0.5);
                endX -= Math.ceil(delta * 0.5)
            }

            for (let j = startX; j < endX; j++) {
                let slot = TileSlot.create(onwer.getWorld());
                onwer.addSlotWithLogicalPos(slot, cc.v2(j, i))
            }
        }
    }

    public getCenterRow(): number {
        return Math.floor(this._mapSize.height * 0.5);
    }

    public getCenterColumn(): number {
        return Math.floor(this._mapSize.width * 0.5);
    }
}
