import TileMapStateBase, { TileMapStateType } from "./TileMapStateBase";
import TileMapStatePool from "./TileMapStatePool";
import TileMap from "../TileMap";

export default class TileMapStateGameOver extends TileMapStateBase {

    public static create(): TileMapStateGameOver {
        let instance: TileMapStateGameOver = TileMapStatePool.getInstance().getState(TileMapStateType.GameOver) as TileMapStateGameOver
        if (!instance) {
            instance = new TileMapStateGameOver();
        }
        instance.init();
        return instance;
    }

    constructor() {
        super(TileMapStateType.GameOver)
    }

    public init() {

    }

    public enter(onwer: TileMap): void {

    }


}
