import { AssetBundleID } from "../../../../../scripts/Defines/Defines";
import EntityBase from "../../../../../scripts/Frameworks/EWS/Entity/EntityBase";
import EntityNodePool from "../../../../../scripts/Frameworks/EWS/Entity/EntityNodePool";
import WorldBase from "../../../../../scripts/Frameworks/EWS/World/WorldBase";
import GameMath from "../../../../../scripts/Frameworks/Utils/GameMath";
import Tile from "../Tile/Tile";

const { ccclass, property } = cc._decorator;
export class TilePieceInfo {
    public levels: number[] = [];
    public angleIndex: number = 0;

    constructor(levels: number[], angleIndex: number) {
        this.levels = levels;
        this.angleIndex = angleIndex;
    }
}

@ccclass
export default class TilePiece extends EntityBase {

    public static create(world: WorldBase, info: TilePieceInfo): TilePiece {
        let entity = EntityNodePool.getInstance().getEntityNode(AssetBundleID.GameScene,
            "prefab/game_layer/TilePiece", "TilePiece");
        if (entity) {
            let tile: TilePiece = entity as TilePiece;
            tile.init(world, info);
            return tile;
        }
        return null;
    }

    @property(Tile)
    tile1: Tile = null;

    @property(Tile)
    tile2: Tile = null;

    private _radius: number = 45;
    private _levels: number[] = [];
    public getLevels(): number[] {
        return this._levels;
    }
    private setLevels(levels: number[]) {
        if (levels.length == 1) {
            this._levels = levels;
            this.tile2.node.active = false;
            this.tile1.node.active = true;
        } else if (levels.length == 2) {
            this._levels = levels;
            this.tile1.node.active = true;
            this.tile1.setLevel(levels[0]);
            this.tile2.node.active = true;
            this.tile2.setLevel(levels[1]);
        } else {
            this._levels = [];
            this.tile1.node.active = false;
            this.tile2.node.active = false;
            console.error("Tile Piece should have 1 or 2 tiles")
        }
    }

    private _angleIndex: number = 0;
    public getAngleIndex(): number {
        return this._angleIndex;
    }
    private setAngleIndex(index: number, animated: boolean = false) {
        if (this._levels.length == 1) {
            this._angleIndex = 0;
            this.tile1.setPosition(cc.v2(0, 0))
            this.tile2.setPosition(cc.v2(0, 0))
        } else {
            this._angleIndex = index % 6;
            let radins = GameMath.degreeToRadian(this._angleIndex * -60 + 180)
            let pos1 = cc.v2(Math.cos(radins) * this._radius, Math.sin(radins) * this._radius)
            let pos2 = pos1.mul(-1);

            this.tile1.node.stopActionByTag(1001);
            this.tile2.node.stopActionByTag(1001);

            if (animated) {
                let move1 = cc.moveTo(0.2, pos1).easing(cc.easeOut(2));
                move1.setTag(1001)
                this.tile1.node.runAction(move1)

                let move2 = cc.moveTo(0.2, pos2).easing(cc.easeOut(2));
                move2.setTag(1001)
                this.tile2.node.runAction(move2)
            } else {
                this.tile1.setPosition(pos1);
                this.tile2.setPosition(pos2);
            }
        }
    }


    public init(world: WorldBase, info: TilePieceInfo) {
        this.initEntity(world);
        this.setTilePieceInfo(info);
    }

    public getTilePieceInfo(): TilePieceInfo {
        let levels = this._levels.slice(0);
        let angleIndex = this._angleIndex;
        let result = new TilePieceInfo(levels, angleIndex);
        return result;
    }
    public setTilePieceInfo(info: TilePieceInfo) {
        this.setLevels(info.levels);
        this.setAngleIndex(info.angleIndex);
    }

    public rotate(): void {
        this.setAngleIndex(this.getAngleIndex() + 1, true)
    }

    public getTiles(): Tile[] {
        let result: Tile[] = [];
        if (this._levels.length == 1) {
            result.push(this.tile1);
        } else if (this._levels.length == 2) {
            result.push(this.tile1);
            result.push(this.tile2);
        } else {
        }
        return result;
    }
}
